import React from "react";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import HtmlCss from "../assets/html_css.jpg";
import Javascript from "../assets/javascript.png";
import Bootstrap from "../assets/bootstrap.jpg";
import ReactJs from "../assets/reactjs.png";
import NodeJs from "../assets/nodejs.png";
import ExpressJs from "../assets/express.png";
import Card from "react-bootstrap/Card";

function Skills() {
  return (
    <Container className="mb-4" id="skills">
      <Row>
        <Col
          className="d-flex flex-wrap pt-3"
          style={{ borderRadius: "10px", backgroundColor: "#739072" }}
        >
          <Col className="col-12 pb-2 text-center">
            <h4>Skills</h4>
          </Col>
          <Col
            className="mb-3 col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4 col-xxl-4"
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              padding: "5px",
            }}
          >
            <Card
              style={{ width: "100%", boxShadow: "0px 0px 18px -3px #000000" }}
            >
              <Card.Img variant="top" src={HtmlCss} />
              <Card.Body
                style={{
                  backgroundColor: "#ECE3CE",
                  borderRadius: "0px 0px 5px 5px",
                }}
              >
                <Card.Text className="text-center">HTML CSS</Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col
            className="mb-3 col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4 col-xxl-4"
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              padding: "5px",
            }}
          >
            <Card
              style={{ width: "100%", boxShadow: "0px 0px 18px -3px #000000" }}
            >
              <Card.Img variant="top" src={Javascript} />
              <Card.Body
                style={{
                  backgroundColor: "#ECE3CE",
                  borderRadius: "0px 0px 5px 5px",
                }}
              >
                <Card.Text className="text-center">Javascript</Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col
            className="mb-3 col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4 col-xxl-4"
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              padding: "5px",
            }}
          >
            <Card
              style={{ width: "100%", boxShadow: "0px 0px 18px -3px #000000" }}
            >
              <Card.Img variant="top" src={Bootstrap} />
              <Card.Body
                style={{
                  backgroundColor: "#ECE3CE",
                  borderRadius: "0px 0px 5px 5px",
                }}
              >
                <Card.Text className="text-center">Bootstrap</Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col
            className="mb-3 col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4 col-xxl-4"
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              padding: "5px",
            }}
          >
            <Card
              style={{ width: "100%", boxShadow: "0px 0px 18px -3px #000000" }}
            >
              <Card.Img variant="top" src={ReactJs} />
              <Card.Body
                style={{
                  backgroundColor: "#ECE3CE",
                  borderRadius: "0px 0px 5px 5px",
                }}
              >
                <Card.Text className="text-center">React Js</Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col
            className="mb-3 col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4 col-xxl-4"
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              padding: "5px",
            }}
          >
            <Card
              style={{ width: "100%", boxShadow: "0px 0px 18px -3px #000000" }}
            >
              <Card.Img variant="top" src={NodeJs} />
              <Card.Body
                style={{
                  backgroundColor: "#ECE3CE",
                  borderRadius: "0px 0px 5px 5px",
                }}
              >
                <Card.Text className="text-center">Node Js</Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col
            className="mb-3 col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4 col-xxl-4"
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              padding: "5px",
            }}
          >
            <Card
              style={{ width: "100%", boxShadow: "0px 0px 18px -3px #000000" }}
            >
              <Card.Img variant="top" src={ExpressJs} />
              <Card.Body
                style={{
                  backgroundColor: "#ECE3CE",
                  borderRadius: "0px 0px 5px 5px",
                }}
              >
                <Card.Text className="text-center">Express Js</Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Col>
      </Row>
    </Container>
  );
}

export default Skills;
