import React from "react";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import VendorPortal from "../assets/vendor portal.jpg";
import Konten from "../assets/konten.jpg";
import CvDistributorTelurSejahtera from "../assets/cv.distributor_telur_sejahtera.jpg";
import SistemInformasiPariwisataPulauNias from "../assets/sistem_informasi_pariwisata_pulau_nias.jpg";
import Card from "react-bootstrap/Card";

function Project() {
  return (
    <Container className="mb-4 " id="project">
      <Row>
        <Col
          className="d-flex flex-wrap pt-3 pb-3"
          style={{ borderRadius: "10px", backgroundColor: "#739072" }}
        >
          <Col className="col-12 pb-2 text-center">
            <h4>Project</h4>
          </Col>
          <Col
            className="mb-1 col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4 col-xxl-4"
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              padding: "5px",
            }}
          >
            <Card
              style={{ width: "100%", boxShadow: "0px 0px 18px -3px #000000" }}
            >
              <Card.Img variant="top" src={VendorPortal} />
              <Card.Body
                style={{
                  backgroundColor: "#ECE3CE",
                  borderRadius: "0px 0px 5px 5px",
                }}
              >
                <Card.Text className="text-center">
                  Vendor Portal PT. Indotek
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col
            className="mb-1 col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4 col-xxl-4"
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              padding: "5px",
            }}
          >
            <Card
              style={{ width: "100%", boxShadow: "0px 0px 18px -3px #000000" }}
            >
              <Card.Img variant="top" src={Konten} />
              <Card.Body
                style={{
                  backgroundColor: "#ECE3CE",
                  borderRadius: "0px 0px 5px 5px",
                }}
              >
                <Card.Text className="text-center">
                  Konten Edukasi di Tiktok
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col
            className="mb-1 col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4 col-xxl-4"
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              padding: "5px",
            }}
          >
            <Card
              style={{ width: "100%", boxShadow: "0px 0px 18px -3px #000000" }}
            >
              <Card.Img variant="top" src={CvDistributorTelurSejahtera} />
              <Card.Body
                style={{
                  backgroundColor: "#ECE3CE",
                  borderRadius: "0px 0px 5px 5px",
                }}
              >
                <Card.Text className="text-center">
                  Sistem Informasi CV. Distributor Telur Sejahtera
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col
            className="mb-1 col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4 col-xxl-4"
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              padding: "5px",
            }}
          >
            <Card
              style={{ width: "100%", boxShadow: "0px 0px 18px -3px #000000" }}
            >
              <Card.Img
                variant="top"
                src={SistemInformasiPariwisataPulauNias}
              />
              <Card.Body
                style={{
                  backgroundColor: "#ECE3CE",
                  borderRadius: "0px 0px 5px 5px",
                }}
              >
                <Card.Text className="text-center">
                  Sistem Informasi Pariwisata Pulau Nias
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Col>
      </Row>
    </Container>
  );
}

export default Project;
