import React from "react";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTiktok } from "@fortawesome/free-brands-svg-icons";
import { faInstagram } from "@fortawesome/free-brands-svg-icons";
import { faYoutube } from "@fortawesome/free-brands-svg-icons";
import { faBlogger } from "@fortawesome/free-brands-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";

import Samo from "../assets/samotuho-ndruru.png";

function Contact() {
  const position = [-6.201075, 106.767713];

  return (
    <Container className=" pb-3" id="contact">
      <Row
        className="pb-4"
        style={{ backgroundColor: "#ffffff", borderRadius: "10px" }}
      >
        <Col className="col-12 pb-2 pt-3 text-center">
          <h4>Contact</h4>
        </Col>
        <Col className="col-12 col-md-12 col-lg-6 col-xl-6 col-xxl-6">
          <Form>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Nama lengkap</Form.Label>
              <Form.Control type="text" placeholder="nama lengkap" />
            </Form.Group>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput2">
              <Form.Label>Email</Form.Label>
              <Form.Control type="email" placeholder="email@gmail.com" />
            </Form.Group>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>Pesan</Form.Label>
              <Form.Control as="textarea" rows={3} />
            </Form.Group>
          </Form>
        </Col>
        <Col className="col-12 col-md-12 col-lg-6 col-xl-6 col-xxl-6">
          <MapContainer
            center={position}
            zoom={15}
            style={{ height: "300px", borderRadius: "10px" }}
          >
            <TileLayer
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            />
            <Marker
              position={position}
              icon={L.icon({ iconUrl: Samo, iconSize: [30, 30] })}
            >
              <Popup>
                Jalan D Nomor 20A, RT 003, RW 001, Kebon Jeruk, Jakarta Barat
              </Popup>
            </Marker>
          </MapContainer>
        </Col>
        <Col
          className="mt-4 ms-3 me-3"
          style={{
            borderRadius: "10px",
            backgroundColor: "#739072",
            boxShadow: "0px 0px 18px -3px #000000",
          }}
        >
          <div className="pt-3 pb-3 d-flex justify-content-around align-items-center">
            <a
              href="https://tiktok.com/@samotuho.ndruru"
              style={{ color: "white" }}
            >
              <FontAwesomeIcon icon={faTiktok} size="2x" />
            </a>
            <a
              href="https://instagram.com/samotuho.ndruru"
              style={{ color: "white" }}
            >
              <FontAwesomeIcon icon={faInstagram} size="2x" />
            </a>
            <a
              href="https://youtube.com/@samotuhondruru"
              style={{ color: "white" }}
            >
              <FontAwesomeIcon icon={faYoutube} size="2x" />
            </a>
            <a
              href="https://www.samotuhondruru.com/"
              style={{ color: "white" }}
            >
              <FontAwesomeIcon icon={faBlogger} size="2x" />
            </a>
            <a href="https://github.com/samotuho" style={{ color: "white" }}>
              <FontAwesomeIcon icon={faGithub} size="2x" />
            </a>
          </div>
        </Col>
      </Row>
    </Container>
  );
}

export default Contact;
