import React from "react";
import Col from "react-bootstrap/esm/Col";
import Container from "react-bootstrap/esm/Container";
import Image from "react-bootstrap/esm/Image";
import Row from "react-bootstrap/esm/Row";
import Sam from "../assets/sam.png";

function AboutMe() {
  return (
    <Container className="mb-4" id="about">
      <Row style={{ borderRadius: "10px", backgroundColor: "#739072" }}>
        <Col xs className="p-0 m-3 text-center   col-md-4 col-lg-4 col-xl-4">
          <Image
            src={Sam}
            alt="Gambar Samotuho Ndruru"
            style={{
              borderRadius: "10px",
              width: "350px",
              boxShadow: "0px 3px 18px -3px #000000",
            }}
          />
        </Col>
        <Col
          className="ms-auto pt-5 col-12 col-md-6 col-lg-6 col-xl-6"
          style={{
            padding: "25px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <div>
            <h4>About Me</h4>
            <p style={{ fontSize: "18px" }}>
              Hai, saya adalah seorang Programming Tutorial Creator di TikTok
              yang senang membagikan pengetahuan tentang coding. Melalui
              video-video singkat, saya berusaha membuat pemrograman terasa
              lebih sederhana dan menyenangkan, sehingga siapa pun dapat memulai
              perjalanan coding mereka dengan percaya diri.
            </p>
          </div>
        </Col>
      </Row>
    </Container>
  );
}

export default AboutMe;
